* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
  
  .card {
    background-color: lightgrey;
    height: 100vh;
    position: relative;
}
  
  .qr-card {
    background-color: white;
    height: 497px;
    width: 320px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    place-items: center;
}

